@import url(https://fonts.googleapis.com/css2?family=Akaya+Telivigala&display=swap);
/** @format */
.App {
  font-family: "Akaya Telivigala", cursive;
  text-align: center;
  margin: auto;
  background-color: #99e6ff;
}

.glass {
  border: solid 5px blueviolet;
  border-radius: 10%;
  width: 70%;
  margin: auto;
  padding: 30px;
  background-color: yellow;
  box-shadow: 30px 30px orangered;
}

img {
  height: 100px;
  width: 100px;
  animation-name: headline-animation;
  animation-duration: 4s;
  animation-delay: 2s;
  animation-iteration-count: infinite;
}
h2 {
  color: blueviolet;
  font-size: 40px;
  animation-name: headline-animation;
  animation-duration: 4s;
  animation-delay: 2s;
  animation-iteration-count: infinite;
}

@media (max-width: 768px) {
  body {
    display: grid;
    grid-template-columns: auto;
  }
}

/** @format */

.animal {
  border-radius: 50%;
  padding: 10px;
  margin: 15px;
  background-color: yellowgreen;
}

/** @format */

.soc-ico {
  width: 50px;
  margin: 10px;
  margin-top: 20px;
  height: 50px;
}
.footer p {
  margin: auto;
}

.footer a {
  font-size: 2.5rem;
  margin: 10px;
}
.footer li {
  list-style: none;
}
.footer .soc-ico:hover {
  animation: footer 0.7s ease;
}
p {
  font-size: 30px;
}
li {
  font-size: 30px;
}
@keyframes footer {
  100% {
    transform: rotate(360deg);
  }
}

/** @format */

.small {
  border: solid 3px #4dffff;
  background-color: #3dc8ff;
  background-image: url("https://www.transparenttextures.com/patterns/nice-snow.png");
  border-radius: 50%;
  width: 40px;
  height: 40px;
  position: relative;
  animation-name: small-animation;
  animation-duration: 4s;
  animation-delay: 1s;
  animation-iteration-count: infinite;
}
@keyframes small-animation {
  0% {
    background-color: #3dc8ff;
    background-image: url("https://www.transparenttextures.com/patterns/nice-snow.png");
  }
  25% {
    background-color: yellow;
  }
  50% {
    background-color: rgb(73, 73, 172);
  }
  75% {
    background-color: plum;
  }
  100% {
    background-color: #3dc8ff;
    background-image: url("https://www.transparenttextures.com/patterns/nice-snow.png");
  }
}
.larg {
  border: solid 3px #4dffff;
  background-color: #0081bd;
  background-image: url("https://www.transparenttextures.com/patterns/asfalt-light.png");
  border-radius: 50%;
  width: 70px;
  height: 70px;
  margin: 10px;
  position: relative;
  animation-name: large-animation;
  animation-duration: 4s;
  animation-delay: 2s;
  animation-iteration-count: infinite;
}
@keyframes large-animation {
  0% {
    background-color: #0081bd;
    background-image: url("https://www.transparenttextures.com/patterns/asfalt-light.png");
  }
  25% {
  }
  50% {
    background-color: rgb(194, 84, 228);
  }
  75% {
    background-color: pink;
    transform: rotate(180deg);
  }
  100% {
    background-color: #0081bd;
    background-image: url("https://www.transparenttextures.com/patterns/asfalt-light.png");
  }
}
.small-up-div {
  padding: 3vw;
  display: grid;
  grid-template-columns: auto auto auto auto auto auto;
}
h1 {
  font-size: 15vh;
  color: #6600ff;
  text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
  position: relative;
  animation-name: headline-animation;
  animation-duration: 4s;
  animation-delay: 2s;
  animation-iteration-count: infinite;
}
@keyframes headline-animation {
  0% {
    color: #0081bd;
  }
  25% {
    color: yellow;
    transform: rotate(20deg);
  }
  50% {
    color: blue;
  }
  75% {
    color: purple;
    transform: rotate(-20deg);
  }
  100% {
    color: #0081bd;
  }
}

/** @format */

.white {
  background-color: whitesmoke;
  height: 60vh;
  width: 5vw;
  position: relative;
  z-index: 0;
}
.black {
  background-color: #333;
  height: 40vh;
  width: 5vw;
  position: relative;
  bottom: 20vh;
  z-index: 1;
}
.piano {
  margin: 15px;
  padding: 30px;
  border: solid 5px;
  border-radius: 15%;
  background-color: whitesmoke;
}
.piano-btn {
  border-radius: 50%;
  padding: 10px;
  margin: 15px;
  background-color: peachpuff;
}
.piano-img {
  border-radius: 50%;
}
@media (max-width: 768px) {
  .black {
    width: 6vw;
    height: 20vh;
    position: relative;
    bottom: 10vh;
  }
  .white {
    width: 6vw;
    height: 30vh;
  }
}

/** @format */

.songs-btn {
  border-radius: 50%;
  padding: 10px;
  margin: 15px;
  background-color: #ff66cc;
  font-family: "Akaya Telivigala", cursive;
}

/** @format */

.vehicles-btn {
  border-radius: 50%;
  padding: 10px;
  margin: 15px;
  background-color: #3a7bdd;
}

